.tangible-template-system--editor-actions-panel {
  background-color: #4c566a;
  color: #fff;
  padding: 0;
  display: flex;
  user-select: none;
}
.tangible-template-system--editor-actions-panel,
.tangible-template-system--editor-actions-panel select[data-action=font-list],
.tangible-template-system--editor-actions-panel select[data-action=theme-list] {
  font-size: 12px;
  line-height: 1;
}
.tangible-template-system--editor-actions-panel > .col-left {
  flex-grow: 1;
}
.tangible-template-system--editor-actions-panel > .col-right {
  display: flex;
  text-align: right;
}
.tangible-template-system--editor-actions-panel .settings-group {
  flex-grow: 1;
  align-items: center;
  padding: 0 0.25rem;
}
.tangible-template-system--editor-actions-panel .setting-label,
.tangible-template-system--editor-actions-panel .setting-label-button {
  margin: 0.125rem 0;
  padding: 0.25rem 0.5rem;
  border-left: 1px solid #848d9d;
}
.tangible-template-system--editor-actions-panel select[data-action=font-list],
.tangible-template-system--editor-actions-panel select[data-action=theme-list] {
  padding: 2px 4px;
  background-color: #2e3440;
  min-height: 0;
  border: 0;
  box-shadow: none;
}
.tangible-template-system--editor-actions-panel select[data-action=font-list], .tangible-template-system--editor-actions-panel select[data-action=font-list]:hover, .tangible-template-system--editor-actions-panel select[data-action=font-list]:focus,
.tangible-template-system--editor-actions-panel select[data-action=theme-list],
.tangible-template-system--editor-actions-panel select[data-action=theme-list]:hover,
.tangible-template-system--editor-actions-panel select[data-action=theme-list]:focus {
  color: #fff;
}
.tangible-template-system--editor-actions-panel [data-action=font-size] {
  padding: 0 4px;
}
.tangible-template-system--editor-actions-panel [data-action=font-size-minus],
.tangible-template-system--editor-actions-panel [data-action=font-size-plus] {
  padding: 4px 4px;
  cursor: pointer;
}
.tangible-template-system--editor-actions-panel .setting-label-button {
  cursor: pointer;
}